<template>
	<div class="container">
		<div class="card">
			<div class="card-action">
				<h4 class="center orange-text text-darken-3">Aslanyan`s Chat</h4>
				<hr>
				<change-name :name="name" :counts="chatInfo" />
				<div class="card-content" ref="content">
					<span></span>
					<ul class="messages">

	
						<li v-for="(chat , index) in chats" :key="index"
								:class="name == chat.name ? 'current delete' : 'another' " @click.self="deleteData">
							
							<input type="hidden" name="hidden_name" :value="name" v-if="name == chat.name">
							<input type="hidden" name="gender"      :value="chat.gender" v-if="name == chat.name">
							
							<span class="name">{{ name == chat.name ? 'You' :  chat.name}}</span>
							<span :class="'sex ' + chat.gender" v-if="name !== chat.name">{{ chat.gender === 'male' ? '&male;' : '&female;' }}</span>
							<p class="message aqua-text" ref="input" >{{chat.text}}</p>
							<span class="time">{{chat.date.time}}</span>
							<span class="date">{{chat.date.date}}</span>

						</li>
	
					</ul>
				</div>
				<new-message :name="name" :content="this.$refs['content']" :issetMessage="chatInfo.curentMessage" />
			</div>
		</div>
	</div>
</template>

<script>
import NewMessage from '@/components/NewMessage';
import ChangeName from '@/components/ChangeName'
import db from '../firebase/init.js';
import util from '../utils/util';
import { firestore } from 'firebase';
import LoginVue from '../components/Login.vue';

/**
 * Creatd By @author Robert Aslanyan
 * May 2018 
 *  
 */
export default {
		components:{
			NewMessage,
			ChangeName
		},
		data(){
			return{
				name:document.cookie.split('=')[1],
				chatInfo:{
					allMessage:0,
					curentMessage:0,
					usersCount:0,
					regUsersCount:0,
				},
				
			}
		},
		firestore() {
			return {
				chats: db.collection('messages').orderBy('createdAt')				
			}
		},
		methods:{

			/**
			 * Delete Current elemnt data
			 * @augments Event
			 */
			deleteData(e){
				let currentEl = e.target; // Element in whcih was clicked
				
				if(currentEl.classList[0] == 'current'){ 						
						let  elText = currentEl.childNodes[4],
							 name 	= currentEl.firstChild.value, // Get name from hidden input 
							 text 	= elText.textContent; // Get Text 

						
						 let userRef = db.collection('messages');

						 userRef.get().then(elements => {

							 elements.forEach(el => {

								 let name  = el.data().name,
								 	 _text = el.data().text;
								 
								  if((text === _text) && (name === this.name)){
									  util.Handler('Your Message was deleted!' , false , 2000);
									  el.ref.delete();
								  }
								 
							 })
						 });
						
				}
			}

		},
		updated(){		
			
			 let messages  = document.querySelectorAll('.message');
			 let regExp = /(https?:)?(\/\/)?(w{3}\.)?(\w{2,19}\.)(\w{1,12}(\.\w+)?)\/*\S+/gi;
			 let arr = [];
						
				
			 // Link text , if the text match to regExp
			 messages.forEach(el => {

					let text = el.textContent;
					let match = text.match(regExp);

					if(text.match(regExp)){
						
						let text = el.textContent;

						 match.forEach((elem , index , arr) => {

							 let hasHttp = elem.includes('http'  , 'https');

									/** Create Link tag with matched regex href and text in it  **/
							 let a = `<a href="${hasHttp ? elem : 'http://' + elem }" target="_blank">${elem}</a>`;
							 
							 /** If Link name is same  **/
							 if(arr.indexOf(elem) !== index)
								 return;

								     /** Find in string match value and replace it by link **/ 							 
							 text = text.replace(elem  , a);

							 el.innerHTML = text;
							 
						 });
					}
				 
			 });

			/** Request For Current User  */
			db.collection('messages').get()
			  .then(data => {
						let curent = [];
						data.forEach(el => {
							if(el.data().name == this.name)
								return curent.push(this.name);
						});
					return this.chatInfo.curentMessage = curent.length;
			  });

            /** Request For All Users  */
			db.collection('messages').get()
			  .then(data => {
					let all = [];
					data.forEach(el => all.push(el.ref.id));

					return this.chatInfo.allMessage = all.length;
			});

			db.collection('messages').get()
			  .then(data => {
					let users = [];
					data.forEach(el => users.push(el.data().name));

						/** Make array unique */
				users = users.filter((el , index ,arr) =>  arr.indexOf(el) == index)


				return this.chatInfo.usersCount  = users.length;
			});

			/** Get All Register Users  */
			db.collection('users').get()
			  .then(data => {

				   let users = [];
					if(!data.empty)
						data.forEach(el => users.push(el.id))
					return this.chatInfo.regUsersCount  = users.length;
				  
				})

		    /** Emoji Parser */
			util.emojiParser(messages);

				
		},
		beforeUpdate(){
			if(document.cookie === '')
				this.$router.push({name:'main' , params:{message:'Time is up'}})
		}



}
</script>

<style lang="scss" scoped>

.card-content{
	max-height: 60vh;
	overflow-y: scroll;
	overflow-x: hidden;
	position: relative;
	z-index: 1;
}

.name{
	text-transform: capitalize;
	margin-right: 0.5rem;
	color:#6B5B95;
	font-weight: 500;
}
.message{
	word-break: break-all;
	font-weight: 600;
	padding: .5rem;
	color:#B18F6A;
	font-size: 1.05rem;
}
.sex{
	font-size: 1.35rem;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
	font-weight: 600;
}
.sex.male{color:#4F84C4;}
.sex.female{color:#E94B3C;}

.time , .date{ 
	display: block; 
    text-align: right;
	color:#2E4A62;
}
.date{ font-size: .75rem;}
.messages{ 
	margin-top: .5rem;
	li{
		margin-top: 1rem;
		padding: 0.5rem;
		border-radius: 10px;
		position: relative;
		
	}
	.current , .another{
		clear:both;
		max-width: 50%;
	}
	.current{
		background: #ede7f6;
		float: right;
	}
	.another{
		background:#bbdefb;
		float: left;
		&:after{
			content: '';
			position: absolute;
			top: calc(50% - 8px);
			transform: rotate(45deg);
			left: -8px;
			z-index: -1;
			height: 16px;
			width: 16px;
			background: inherit;
		}

	}
}
.delete{
	position: relative;
	cursor: pointer;
	overflow: hidden;
	&:hover  {
			transform: scale(1.1);
			&:before{
			font-family: 'Material Icons';
			content: 'delete';
			font-size: 2rem;
			color:#fff;
			text-align: center;
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 50%;
			background:rgba(#333 , .65);
		}
	}
}
</style>




